.alerts-drawer__cell-name[data-v-06d54e34] {
  font-weight: 500;
  max-width: 240px;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}
.alerts-drawer__cell-muted[data-v-06d54e34] {
  color: #9ca3af;
  font-size: 12px;
}
.alerts-drawer__stack-tag[data-v-06d54e34] {
  font-size: 11px;
  color: var(--color-text-muted, #9ca3af);
  background: #f5f6fa;
  padding: 3px 8px;
  border-radius: 4px;
  white-space: nowrap;
}
.alerts-drawer__overdue-tag[data-v-06d54e34] {
  font-size: 11px;
  font-weight: 600;
  color: #b91c1c;
  background-color: #fde8e8;
  padding: 3px 8px;
  border-radius: 4px;
  white-space: nowrap;
}

/*  Show More  */
.alerts-drawer__show-more[data-v-06d54e34] {
  background: none;
  border: none;
  color: #4a90d9;
  font-size: 12px;
  font-weight: 500;
  cursor: pointer;
  padding: 6px 0;
  margin-top: 4px;
}
.alerts-drawer__show-more[data-v-06d54e34]:hover {
  text-decoration: underline;
}

/*  Section (Stalled/Health)  */
.alerts-drawer__section[data-v-06d54e34] {
