}
.alerts-drawer__section[data-v-06d54e34]:last-child {
  margin-bottom: 0;
}
.alerts-drawer__section-title[data-v-06d54e34] {
  font-size: 13px;
  font-weight: 600;
  color: var(--color-text-secondary, #6b7280);
  text-transform: uppercase;
  letter-spacing: 0.04em;
  margin: 0 0 12px 0;
  padding: 0 0 8px 0;
  border: none;
  border-bottom: 2px solid #f0f1f5;
}

/*  Drawer Transition  */
.drawer-enter-active[data-v-06d54e34] {
  transition: opacity 0.25s ease;
}
.drawer-enter-active .alerts-drawer[data-v-06d54e34] {
  animation: slideIn-06d54e34 0.25s ease forwards;
}
.drawer-leave-active[data-v-06d54e34] {
  transition: opacity 0.2s ease;
}
.drawer-leave-active .alerts-drawer[data-v-06d54e34] {
  animation: slideOut-06d54e34 0.2s ease forwards;
}
.drawer-enter[data-v-06d54e34],
.drawer-leave-to[data-v-06d54e34] {
  opacity: 0;
}
@keyframes slideIn-06d54e34 {
from {
    transform: translateX(100%);
}
to {
    transform: translateX(0);
}
}
@keyframes slideOut-06d54e34 {
from {
    transform: translateX(0);
}
to {
    transform: translateX(100%);
}
}

/*  Responsive  */
@media (max-width: 1024px) {
.alerts-panel__summary-strip[data-v-06d54e34] {
    grid-template-columns: repeat(3, 1fr);
}
.alerts-drawer[data-v-06d54e34] {
    width: 480px;
}
}
@media (max-width: 640px) {
.alerts-panel__summary-strip[data-v-06d54e34] {
    grid-template-columns: repeat(2, 1fr);
}
.alerts-drawer[data-v-06d54e34] {
    width: 100vw;
    max-width: 100vw;
}
}
